import React, { useState } from 'react';
import { Flex, Text, Box } from 'rebass';
import { useStaticQuery, graphql } from 'gatsby';
import GatsbyLink from 'gatsby-link';
import styled from '@emotion/styled';
import { useScrollPosition } from '@n8tb1t/use-scroll-position';

import Menu from './Menu';
import Account from './Icons/Account';
import Search from './Search';
import ShoppingBag from './Icons/ShoppingBag2';
import ShoppingCart from './Icons/ShoppingCart';
import strings from './strings.json';

const { ariaAccountLabel, ariaShoppingCartLabel, ariaHomaPageLinkLabel } = strings;

const NavbarBase = styled.nav`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1030;
  background: red;
`;

const Nav = styled(NavbarBase)`
  visibility: ${(props) => (props.show ? 'visible' : 'hidden')};
  transition: all 200ms ${(props) => (props.show ? 'ease-in' : 'ease-out')};
  transform: ${(props) => (props.show ? 'none' : 'translate(0, -100%)')};
`;

const validURL = (str) => {
  const pattern = new RegExp(
    '^(https?:\\/\\/)?' + // protocol
      '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
      '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
      '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
      '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
      '(\\#[-a-z\\d_]*)?$',
    'i'
  ); // fragment locator
  return !!pattern.test(str);
};

const Navbar = (props) => {
  const [hideNavbarOnScroll, setHideNavbarOnScroll] = useState(true);

  useScrollPosition(
    ({ prevPos, currPos }) => {
      // Note: prevPos.y > -12 is here to fix Nav component disappearing bug
      // due to elastic scrolling/bounce effect in mobile Safari.
      const isShow = currPos.y > prevPos.y || prevPos.y > -12;
      if (isShow !== hideNavbarOnScroll) setHideNavbarOnScroll(isShow);
    },
    [hideNavbarOnScroll],
    null,
    false,
    100
  );

  const data = useStaticQuery(graphql`
    {
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            storeName
            logo {
              url
              width
              height
            }
            menu {
              handle
              id
              link
              name
              parentId
              type
            }
            topNavMenu {
              name
              link
            }
          }
        }
      }
    }
  `);

  const {
    storeName,
    logo,
    menu,
    topNavMenu,
  } = data.site.siteMetadata.gatsbyStorefrontConfig;

  return (
    <Nav px={["5vw", "3.5vw"]} show={hideNavbarOnScroll}>
      <Box py={["3vw", "1.5vw"]} width={1} as="nav" bg="white">
        <Box
          sx={{ 
            maxWidth: 1300,
            borderBottom: '1px solid #e6e6e6'
          }}
          justifyContent="left"
          alignItems="left"
          display={['none', 'flex']}
          pb={[2, null, 3]}
        >
          <Flex sx={{ textTransform: 'uppercase', fontSize: '.8vw'}} >
            {topNavMenu
              ? topNavMenu.map((link, index) => {
                if (validURL(link.link)) {
                  return (
                    <Text key={index}> 
                      <Link href={link.link}>{link.name}</Link>
                    </Text>
                  );
                } else {
                  return (
                    <Text key={index}>
                      <GatsbyLink to={link.link}>
                        {link.name}
                      </GatsbyLink>
                    </Text>
                  );
                }
              })
            : ''}
          </Flex>
        </Box>

        {/* Mobile Nav */}
        <Box
          display={['flex', 'none']}
          style={{ maxWidth: 1300 }}
          justifyContent="center"
          alignItems="center"
          mx="auto"
          px={[3, null, 4]}
        >
          <Flex>
            <Box width={25}>
              <Menu menu={menu} />
            </Box>

            <Text
              as={GatsbyLink}
              aria-label={ariaAccountLabel}
              to=""
              fontSize={4}
              style={{ textDecoration: 'none' }}
              ml="auto"
            >
              <Account width="25px" height="25px" color="primary" />
            </Text>
          </Flex>



          <Text
            as={GatsbyLink}
            to="/"
            aria-label={ariaHomaPageLinkLabel}
            style={{ textDecoration: 'none' }}
            ml="auto"
          >
            {logo ? (
              <img
                src={logo.url}
                width={logo.width}
                height={logo.height}
                alt={storeName}
              />
            ) : (
              <Flex>
                <ShoppingBag
                  width={['25px', '30px']}
                  height={['25px', '30px']}
                  color="primary"
                />

                <Text
                  ml={2}
                  color="primary"
                  fontSize={[2, 3]}
                  sx={{ display: ['none', 'block'] }}
                >
                  {storeName}
                </Text>
              </Flex>
            )}
          </Text>

          <Flex ml="auto" width={100}>
            <Box ml="auto">
              <Search width="25px" height="25px" color="primary" />
            </Box>

            <Text
              as={GatsbyLink}
              aria-label={ariaShoppingCartLabel}
              to="/cart"
              fontSize={4}
              style={{ textDecoration: 'none' }}
              ml="auto"
            >
              <ShoppingBag width="25px" height="25px" color="primary" />
            </Text>
          </Flex>
        </Box>
      </Box>
    </Nav>
  );
};

export default React.memo(Navbar);
